export const mockData = rejudge.table.mockData;

// N
const imageMap = [timg1,timg2,timg3,timg4,timg5]

// 
export const imageInfos = [];

for (let i = 0; i < imageMap.length; i++) {
  if (i%2 === 0) {
    imageInfos.push({
      code: `SPASD` + i, // code
      create_time: '', //
      id: `code-${i}`,
      img_name: '', //
      img_src_path: imageMap[i], //
      img_src_time: '', //
      img_store_path: '', //
      modify_time: '', //
      op: '', //
      sample_id: '', //
      status: '', //
      valid_code: '', //
      rework: true, // 
      turn_on: false, //
      sheet_type: true, // 
      // code_type: codeTypeInfos() // code
    })
  } else {
    imageInfos.push({
      code: '', // code
      create_time: '', //
      id: `code-${i}`,
      img_name: '', //
      img_src_path: '', //
      img_src_time: '', //
      img_store_path: '', //
      modify_time: '', //
      op: '', //
      sample_id: '', //
      status: '', //
      valid_code: '', //
      imageUrl: imageMap[i],
      codeType: ``, // code
      rework: false, // 
      turn_on: false, //
      sheet_type: false, // 
      // code_type: codeTypeInfos() // code
    })
  }
}
export function codeTypeInfos () {
  let _arr = [];
  for(let i = 0; i < 10 ; i++) {
    _arr.push({id: 'a' + i, codeType: 'SPASD'+i})
  }
  return _arr
}

