import moment from 'moment'
import { TopTips } from '@tencent/bere'
import queryString from 'query-string'

//  restful filters
function combineFilters(arg) {
  const result = []
  Object.keys(arg).forEach(key => {
    if (!arg[key]) {
      return
    }
    result.push({ name: key, op: '==', val: arg[key] })
  })
  return result
}

// 
function buildImageListParam({ page, limit, filters }) {
  const {
    site,
    shop,
    operation,
    product,
    startDate,
    endDate,
    glassId,
    confidence,
    uniformityOnly
  } = filters
  // glassId 
  const glassFilter = glassId[0]
    ? [{ name: 'glass_id', op: 'in', val: glassId }]
    : [...combineFilters({ site, shop, operation, product })]

  const param = {
    queryString: { page, results_per_page: limit },
    filters: [
      ...glassFilter,
      { name: 'timestamp', op: '>=', val: startDate },
      { name: 'timestamp', op: '<=', val: endDate },
      { name: 'confidence_level', op: '>=', val: confidence }
    ]
  }

  if (uniformityOnly) {
    param.filters.push({ name: 'code_uniformity', op: '==', val: 1 })
  }
  return param
}

// 
function normalizeReq({ method, url, data }, config = {}) {
  data = { ...data }

  const regex = /\/:(\w+)/g //  url 
  const params = []
  let matches
  while ((matches = regex.exec(url)) != null) {
    if (matches[1]) {
      params.push(matches[1])
    }
  }
  params != null &&
    params.forEach(function (name) {
      let d = data[name]
      delete data[name]
      if (d == null) {
        d = ''
      }
      url = url.replace(`:${name}`, d)
    })

  if (data.queryString) {
    url += '?' + queryString.stringify(data.queryString, { arrayFormat: 'bracket' })
    delete data.queryString
  }

  return { url, data }
}

const defaultState = {
  // 
  total: 0,
  // 
  limit: 15,
  // 
  page: 1,
  // 
  imageList: [],
  // 
  options: {
    site: [],
    shop: [],
    operation: [],
    product: [],
    minDate: moment()
      .subtract(30, 'days')
      .startOf('day')
      .format('YYYY-MM-DD HH:mm:ss'),
    maxDate: moment()
      .endOf('day')
      .format('YYYY-MM-DD HH:mm:ss')
  },
  // 
  filters: {
    // glass_id 
    glassId: [],
    // 
    site: '',
    // 
    shop: '',
    // 
    operation: '',
    // 
    product: '',
    // 
    confidence: 96,
    // 
    uniformityOnly: true,
    // 
    startDate: moment()
      .startOf('day')
      .format('YYYY-MM-DD HH:mm:ss'),
    endDate: moment()
      .endOf('day')
      .format('YYYY-MM-DD HH:mm:ss')
  },
  // 
  image: {
    //  id
    id: '',
    // ADC 
    adc_result: {},
    // MES 
    mes_result: {},
    // 
    rejudge_result: {
      code: null,
      rework: null,
      turn_on: null,
      canBilling: null
    },
    // 
    site: '',
    // 
    shop: '',
    // 
    operation: '',
    // 
    product: '',
    // 
    image_url: '',
    image_path: '',
    image_name: '',
    glass_id: ''
  },
  // 
  codes: [],
  // (imageInfo)(imageList)
  curTab: 'imageList',
  //  ADC  MES 
  showResult: true,
  // 
  isSubmitting: false,
  // 
  isListLoading: true,
  // code 
  isCodeLoading: true
}

export default {
  namespace: 'rejudge',
  state: defaultState,
  reducers: {
    // 
    initialize() {
      return defaultState
    },
    // 
    changeOption(state, action) {
      return { ...state, options: { ...state.options, ...action.payload } }
    },
    // 
    changeFilter(state, action) {
      return { ...state, filters: { ...state.filters, ...action.payload } }
    },
    // 
    clearFilter(state, action) {
      return { ...state, filters: { ...defaultState.filters } }
    },
    // 
    changeImage(state, action) {
      return { ...state, image: { ...state.image, ...action.payload } }
    },
    // 
    changeRejudgeResult(state, action) {
      return {
        ...state,
        image: {
          ...state.image,
          rejudge_result: { ...state.image.rejudge_result, ...action.payload }
        }
      }
    },
    // 
    clearRejudge(state) {
      return {
        ...state,
        image: {
          ...state.image,
          rejudge_result: defaultState.image.rejudge_result
        }
      }
    }
  },

  effects: {
    // select
    * fetchFilterOptions(action, { put, call, context }) {
      const { resp } = yield call(context.service.getFilterOptions)
      if (resp && !resp.code) {
        yield put({ type: 'change', payload: { options: resp.data } })
        if (action.callback) {
          action.callback(resp.data)
        }
      }
    },

    // 
    * fetchCurImageList(action, { select, put, call, context }) {
      yield put({ type: 'change', payload: { isListLoading: true } })
      // 
      const { page, limit, filters } = yield select(state => state.rejudge)
      const param = buildImageListParam({ page, limit, filters })

      const { resp } = yield call(context.service.getImageList, param, { normalizeReq })

      if (resp && resp.code === 0) {
        yield put({ type: 'change', payload: { imageList: resp.data, total: resp.num_results } })

        if (action.callback) {
          action.callback(resp.data)
        }
        yield put({ type: 'change', payload: { isListLoading: false } })
      }

      //  glassId  code
      if (!filters.glassId[0]) {
        yield put({ type: 'fetchCodeList', payload: { ...filters } })
      }
    },

    // 
    * fetchImageList(action, { put, call, context }) {
      const { page, filters } = action.payload

      const param = buildImageListParam({ page, filters, limit: 100 })
      const { resp } = yield call(context.service.getImageList, param, { normalizeReq })

      if (resp && resp.code === 0 && action.callback) {
        action.callback(null, resp.data)
      } else {
        action.callback(resp)
      }
    },

    // 
    * changePage(action, { put }) {
      const { page } = action.payload
      yield put({ type: 'change', payload: { page } })
      yield put({ type: 'fetchCurImageList' })
    },

    // 
    * changePerPage(action, { put }) {
      const { limit } = action.payload
      yield put({ type: 'change', payload: { limit } })
      yield put({ type: 'fetchCurImageList' })
    },

    // code
    * fetchCodeList(action, { put, call, context }) {
      const { site, shop, operation, product } = action.payload
      const param = {
        q: JSON.stringify({
          filters: [...combineFilters({ site, shop, operation, product })]
        })
      }

      yield put({ type: 'change', payload: { isCodeLoading: true } })
      const { resp } = yield call(context.service.fetchCodeList, param)
      if (resp && !resp.code) {
        const codes = resp.data[0] && resp.data[0].codes
        yield put({
          type: 'change',
          //  JSON 
          payload: { codes: JSON.parse((codes || '[]').replace(/'/g, '"')) }
        })
        yield put({ type: 'change', payload: { isCodeLoading: false } })
      }
    },

    // 
    * startRejudge(action, { put, select }) {
      const { site, shop, operation, product } = action.payload
      const { glassId } = yield select(state => state.rejudge.filters)
      // 
      const rejudge_result = action.payload.rejudge_result
        ? JSON.parse(action.payload.rejudge_result)
        : defaultState.image.rejudge_result
      yield put({ type: 'changeImage', payload: { ...action.payload, rejudge_result } })
      yield put({ type: 'change', payload: { curTab: 'imageInfo' } })
      //  glassId  code
      if (glassId[0]) {
        yield put({ type: 'fetchCodeList', payload: { site, shop, operation, product } })
      }
    },

    // 
    * submitRejudge(action, { select, call, context, put }) {
      const { image, imageList } = yield select(state => state.rejudge)

      const param = {
        ...image,
        rejudge_result: JSON.stringify(image.rejudge_result)
      }
      // 
      const { resp } = yield call(context.service.redataGlass, param, { normalizeReq })
      if (resp && !resp.code) {
        TopTips({ theme: 'success', message: '' })
        // 
        const index = imageList.findIndex(item => item.id === image.id)
        const newList = [...imageList]
        newList[index] = param
        yield put({ type: 'change', payload: { imageList: newList } })
        if (action.callback) {
          action.callback()
        }
      }
    },

    // 
    * updateCode(action, { select, put, call, context }) {
      const { codes } = action.payload
      const { site, shop, operation, product } = yield select(state => state.rejudge.image)
      const { resp } = yield call(context.service.updateCodeList, {
        codes,
        site,
        shop,
        operation,
        product,
        queryString: { site, shop, operation, product }
      })
      if (resp && !resp.code) {
        yield put({ type: 'fetchCodeList', payload: { site, shop, operation, product } })
        TopTips({ theme: 'success', message: 'CODE' })
      }
    },

    * nextImage(action, { select, put, context }) {
      const { image, imageList, page, total, limit } = yield select(state => state.rejudge)
      const index = imageList.findIndex(item => item.id === image.id)

      // 
      if (index === imageList.length - 1) {
        if (Math.ceil(total / limit) === page) {
          return TopTips({ theme: 'error', message: '' })
        }
        // 
        yield put({ type: 'change', payload: { page: page + 1 } })
        yield put({
          type: 'fetchCurImageList',
          callback(imageList) {
            if (!imageList[0]) {
              return TopTips({ theme: 'error', message: '' })
            }
            // 
            context.app._store.dispatch({
              type: 'rejudge/startRejudge',
              payload: { ...imageList[0] }
            })
          }
        })
      } else {
        yield put({ type: 'startRejudge', payload: { ...imageList[index + 1] } })
      }
    },

    * prevImage(action, { select, put, context }) {
      const { image, imageList, page } = yield select(state => state.rejudge)
      const index = imageList.findIndex(item => item.id === image.id)

      // 
      if (index === 0) {
        if (page === 1) {
          return TopTips({ theme: 'error', message: '' })
        }
        // 
        yield put({ type: 'change', payload: { page: page - 1 } })
        yield put({
          type: 'fetchCurImageList',
          callback(imageList) {
            // 
            context.app._store.dispatch({
              type: 'rejudge/startRejudge',
              payload: { ...imageList[imageList.length - 1] }
            })
          }
        })
      } else {
        yield put({ type: 'startRejudge', payload: { ...imageList[index - 1] } })
      }
    }
  }
}
