import _ from 'lodash/fp'

export const DEFAULT_VPORT = 3306

export const STATUS = {
  creating: 0, // 
  doing: 1, // 
  ok: 2, // 
  notInit: 3, // 
  rebooting: 4, // 
  expired: -1, // 
  deleted: -2, // 
  deleting: -3 // 
}

export const STATUS_TITLE = {
  [STATUS.creating]: '',
  [STATUS.doing]: '',
  [STATUS.ok]: '',
  [STATUS.notInit]: '',
  [STATUS.rebooting]: '',
  [STATUS.expired]: '',
  [STATUS.deleted]: '',
  [STATUS.deleting]: ''
}

export const STATUSES = _.keys(STATUS_TITLE).map(status => ({
  text: STATUS_TITLE[status],
  value: status
}))
